import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import {ConfirmBookingPageModule} from '../pages/confirm-booking/confirm-booking.module';

import {NguiMapModule} from '@ngui/map';
import {RegisterPageModule} from '../pages/register/register.module';
import {StartPageModule} from '../pages/start/start.module';
import {CreatePasswordPageModule} from '../pages/create-password/create-password.module';
import {ConfirmationCodePageModule} from '../pages/confirmation-code/confirmation-code.module';
import { SetTimeAppointmentPageModule } from "../pages/set-time-appointment/set-time-appointment.module";
import { TreatmentprovidersPageModule } from "../pages/treatmentproviders/treatmentproviders.module";
import { ListprovidersPageModule } from "../pages/listproviders/listproviders.module";
import { SetDayAppointmentPageModule } from "../pages/set-day-appointment/set-day-appointment.module";
import {LoginService} from '../pages/service/login.service';
import {Facebook} from '@ionic-native/facebook';
import {StatusBar} from '@ionic-native/status-bar';
import {SplashScreen} from '@ionic-native/splash-screen';
import {GooglePlus} from '@ionic-native/google-plus';

import { NgCalendarModule } from 'ionic2-calendar';
import {LogInMeccapanPage} from '../pages/log-in-meccapan/log-in-meccapan';
import {SignInPageModule} from '../pages/sign-in/sign-in.module';

@NgModule({
  declarations: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
    LogInMeccapanPage
  ],
  imports: [
    NgCalendarModule,
    BrowserModule,
    ListprovidersPageModule,
    TreatmentprovidersPageModule,
    SetTimeAppointmentPageModule,
    SetDayAppointmentPageModule,  
    ConfirmBookingPageModule,
    RegisterPageModule,     
    StartPageModule,
    CreatePasswordPageModule,
    ConfirmationCodePageModule,
    SignInPageModule,
    NguiMapModule.forRoot({ apiUrl: 'https://maps.google.com/maps/api/js?key=AIzaSyBSy0GEQeCrUgJ9LvrYHUBemGUjHE1PhcU' }),
    IonicModule.forRoot(MyApp, {
      scrollAssist: false,
      autoFocusAssist: false
    }, 
{
  links: [
    { loadChildren: '../pages/confirm-booking/confirm-booking.module.ngfactory#ConfirmBookingPageModuleNgFactory', name: 'ConfirmBookingPage', segment: 'confirm-booking', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/confirmation-code/confirmation-code.module.ngfactory#ConfirmationCodePageModuleNgFactory', name: 'ConfirmationCodePage', segment: 'confirmation-code', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/create-password/create-password.module.ngfactory#CreatePasswordPageModuleNgFactory', name: 'CreatePasswordPage', segment: 'create-password', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/listproviders/listproviders.module.ngfactory#ListprovidersPageModuleNgFactory', name: 'ListprovidersPage', segment: 'listproviders', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/set-day-appointment/set-day-appointment.module.ngfactory#SetDayAppointmentPageModuleNgFactory', name: 'SetDayAppointmentPage', segment: 'set-day-appointment', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/set-time-appointment/set-time-appointment.module.ngfactory#SetTimeAppointmentPageModuleNgFactory', name: 'SetTimeAppointmentPage', segment: 'set-time-appointment', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sign-in/sign-in.module.ngfactory#SignInPageModuleNgFactory', name: 'SignInPage', segment: 'sign-in', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/start/start.module.ngfactory#StartPageModuleNgFactory', name: 'StartPage', segment: 'start', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/treatmentproviders/treatmentproviders.module.ngfactory#TreatmentprovidersPageModuleNgFactory', name: 'TreatmentprovidersPage', segment: 'treatmentproviders', priority: 'low', defaultHistory: [] }
  ]
}),
    
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    TabsPage,
    LogInMeccapanPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    GooglePlus,
    Facebook,
    LoginService,
  ]
})
export class AppModule { }



